"use client"

import React, { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Card } from '@/components/ui/card'

export default function ContactSection() {
  const [loading, setLoading] = useState(false)
  const [status, setStatus] = useState<'idle' | 'success' | 'error'>('idle')

  const onSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault()
    setLoading(true)
    setStatus('idle')

    try {
      const form = event.currentTarget
      const formData = new FormData(form)

      // required by web3forms
      formData.append('access_key', 'gbqtqvzu6mz13w')

      const payload = Object.fromEntries(formData.entries())
      const res = await fetch('https://api.web3forms.com/submit', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Accept: 'application/json',
        },
        body: JSON.stringify(payload),
      })

      const json = await res.json()
      if (json.success) {
        setStatus('success')
        form.reset()
      } else {
        setStatus('error')
       
      }
    } catch (err) {
      setStatus('error')
      console.error(err)
    } finally {
      setLoading(false)
    }
  }

  return (
    <section className="py-32 px-4 lg:px-8">
      <div className="mx-auto max-w-3xl hover:border-purple-500 px-8 lg:px-0">
        <h1 className="text-center text-purple-600 text-4xl font-semibold lg:text-5xl">
          Contact Reevio
        </h1>
        <p className="mt-4 text-center">
          We'll help you find the right plan and pricing for your videos.
        </p>

        <Card className="mx-auto mt-12 max-w-lg p-8 shadow-md sm:p-16">
          <div>
            <h2 className="text-xl font-semibold">Let's get you to the right place</h2>
            <p className="mt-4 text-sm">
              Reach out to our Reevio team! We’re eager to learn more about how you plan to use our
              services.
            </p>
          </div>

          <form onSubmit={onSubmit} className="mt-12 space-y-6" noValidate>
            <div>
              <Label htmlFor="name">Full name</Label>
              <Input id="name" name="name" type="text" required className="mt-2" />
            </div>

            <div>
              <Label htmlFor="email">Email</Label>
              <Input id="email" name="email" type="email" required className="mt-2" />
            </div>

            <div>
              <Label htmlFor="plan">Plan Selection</Label>
              <select
                id="plan"
                name="plan"
                required
                className="mt-2 w-full rounded-md border px-3 py-2 text-sm"
                defaultValue=""
              >
                <option value="" disabled>
                  Select your plan
                </option>
                <option value="basic">Basic</option>
                <option value="pro">Pro</option>
                <option value="premium">Premium</option>
              </select>
            </div>

            <div>
              <Label htmlFor="msg">Message</Label>
              <Textarea id="msg" name="message" rows={3} className="mt-2" />
            </div>

            <div className="flex items-center justify-between gap-4">
              <Button type="submit" disabled={loading}>
                {loading ? 'Sending…' : 'Submit'}
              </Button>

              {status === 'success' && (
                <p className="text-sm text-green-600">Thanks — your message was sent.</p>
              )}
              {status === 'error' && (
                <p className="text-sm text-rose-600">Something went wrong. Please try again.</p>
              )}
            </div>
          </form>
        </Card>
      </div>
    </section>
  )
}
